import * as React from "react"
import './web3connect.scss'
import Button from "@material-ui/core/Button";
import Tooltip from "@material-ui/core/Tooltip";

const ConnectAccount = () => {
    const snowgeTokenAddress='0x5e9280d53f28281ce098c8f64e49f5f5dc9ea185';
    const testnetSnowgeTokenAddress='0x2b5c0653486b6486acab387eb110dd1753f37d95';
    const snowgeABI = [
      // balanceOf
      {
        "constant":true,
        "inputs":[{"name":"_owner","type":"address"}],
        "name":"balanceOf",
        "outputs":[{"name":"balance","type":"uint256"}],
        "type":"function"
      },
      // decimals
      {
        "constant":true,
        "inputs":[],
        "name":"decimals",
        "outputs":[{"name":"","type":"uint8"}],
        "type":"function"
      }
    ]

    const [blockNr, setBlockNr] = React.useState(0);
    const [snowgeBal, setSnowgeBal] = React.useState();
    const [bnbBal, setBNBBal] = React.useState();
    const [account, setAccount] = React.useState(
      localStorage.getItem("account") || ''
    );
    // const [account, setAccount] = React.useState();
    const [chainId, setChainId] = React.useState();
    const canConnect = typeof window !== "undefined";

    // React.useEffect(() => {
    //  const checkConnection = async () => {
    //
    //      // Check if browser is running Metamask
    //      let web3;
    //      if (window.ethereum) {
    //          web3 = new window.Web3(window.ethereum);
    //      } else if (window.web3) {
    //          web3 = new window.Web3(window.web3.currentProvider);
    //      };
    //
    //      // Check if User is already connected by retrieving the accounts
    //      web3.eth.getAccounts()
    //          .then(async (addr) => {
    //              // Set User account into state
    //              if(typeof account !== "undefined") {
    //                localStorage.setItem("account", addr);
    //              }
    //          });
    //    };
    //    checkConnection();
    // }, []);

    React.useEffect(() => {
      if(typeof account !== "undefined") {
        localStorage.setItem("account", account)
        getSnowgeBalance(account);
      }
    }, [account])

    React.useEffect(() => {
      const checkConnection = async () => {
        // Reload on
        window.ethereum.on('chainChanged', (_chainId) => window.location.reload());
        window.ethereum.on('accountsChanged', function (accounts) {
          // Time to reload your interface with accounts[0]!
            localStorage.setItem("account", accounts[0]);
            setAccount(accounts[0]);
            getSnowgeBalance(account);
        });
      };
      checkConnection();
    }, []);

    async function getBlockNumber() {
        console.log('Init web3');
        const web3 = new window.Web3(window.ethereum);
        const currentBlockNumber = await web3.eth.getBlockNumber();
        setBlockNr(currentBlockNumber);
    }

    async function connectAccount() {
      // window.ethereum.on('chainChanged', (_chainId) => window.location.reload());
      console.log('attempting to connect via web3');
      const web3 = new window.Web3(window.ethereum);
      console.log(web3);
      const chainId = await web3.eth.getChainId();
      setChainId(chainId);
      console.log(chainId);
      if(chainId !== 56 & chainId !== 97) {
        window.ethereum.request({ method: 'wallet_addEthereumChain', params: [{ chainId: '0x38', chainName: 'Binance Smart Chain', nativeCurrency: { name: 'BNB', symbol: 'BNB', decimals: 18 }, rpcUrls: ['https://bsc-dataseed.binance.org/'], blockExplorerUrls: ['https://bscscan.com/'] }] });
      }
      const accounts = await web3.eth.requestAccounts();
      const account = accounts[0];
      console.log(account);

      setAccount(account);
      getSnowgeBalance(account, chainId);
    }

    async function disconnectAccount() {
      localStorage.removeItem("account");
      setAccount("");
    }

    async function getSnowgeBalance(account, chainId) {
      const web3 = new window.Web3(window.ethereum);
      var contract;
      if (chainId === 56) {
        contract = new web3.eth.Contract(snowgeABI, snowgeTokenAddress);
      } else if (chainId === 97) {
        contract = new web3.eth.Contract(snowgeABI, testnetSnowgeTokenAddress);
      } else {
        return;
      }
      contract.methods.balanceOf(account).call((error, balance) => {
        contract.methods.decimals().call((error, decimals) => {
          balance = balance / (10**decimals);
          setSnowgeBal(balance.toString());
        });
      });
      let bnbBal = await web3.eth.getBalance(account);;
      let bnbString = (bnbBal / (10**18)).toString();
      setBNBBal(bnbString);
    }

    return (
      <div className={'connect-button-status'}>
          {!account && <Button className={'connect-wallet-button'} onClick={connectAccount}>Connect Wallet</Button>}
          {account && <Tooltip title={<span style={{ whiteSpace: 'pre-line' }}>{`Account: `+ account + `\nSnowgeCoin Balance: ` + snowgeBal + `\nBNB Balance: ` + bnbBal}</span>} enterTouchDelay={0} leaveTouchDelay={4000} interactive={true} enterDelay={200}><div className="connected-text">Connected.</div></Tooltip>}
          {account && <Button className={'connect-wallet-button disconnect-button'} onClick={disconnectAccount}>Disconnect Wallet</Button>}
      </div>
    )
};

export default ConnectAccount
